import React from 'react';
import movies from '../movies.js';
import { Link } from 'react-router-dom';

const Trending = () => {
  const trendingMovies = movies.filter(movie => movie.type === 'trending');

  return (
    <div className="container-fluid">
      <div className="row">
        <h4 className="movie-heading">Trending</h4>
        {trendingMovies.map(({ id, cardImg }, key) => (
          <div key={key} className="col-lg-3 col-md-6 col-6">
            <div className="card movie-card">
              <Link to={`/detail/${id}`}>
                <img src={cardImg} className="card-img-top" alt="Movie" id={id} />
              </Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Trending;
